import { graphql } from 'gatsby'
import React from 'react'

const pageTemplate = props => {

 const data = {
	  nodeFood:  props.pageContext.data
  }

  console.log(data);


  return (  
    <div>
      <h1>{data.nodeFood.title}</h1>
      <p>{data.nodeFood.cookingTime}</p>
    </div>
  )
}



export default pageTemplate 
